import React, { memo } from 'react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { makeStyles, Theme, createStyles } from '@material-ui/core';

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    root: {
      '& > h2, & > h3, & > h4, & > h5': {
        marginTop: '1.5rem',
        marginBottom: '1rem',
      },
      '& h2': {
        fontSize: '2rem',
        fontWeight: 'bold',
      },
      '& p': {
        fontSize: '1rem',
        lineHeight: 1.8,
      },
      '& code.language-text': {
        color: '#ff3860',
        backgroundColor: '#f9f1f3',
        padding: '1px 4px',
        borderRadius: '4px',
        fontSize: '0.85rem',
        '&::before, &::after': {
          content: '""',
        },
      },
      '& blockquote': {
        position: 'relative',
        padding: 16,
        fontStyle: 'italic',
        backgroundColor: '#eeeeee',
        '&::before': {
          position: 'absolute',
          top: 0,
          left: 0,
          width: 5,
          height: '100%',
          content: '""',
          backgroundColor: '#dddddd',
        },
      },
      '& span.gatsby-resp-image-wrapper': {
        borderRadius: '8px',
        overflow: 'hidden',
        margin: '2.5rem 0 1rem',
        '& + small': {
          display: 'block',
          color: theme.palette.text.secondary,
          marginBottom: '2.5rem',
        },
      },
      '& p > a': {
        color: theme.palette.primary.main,
        textDecoration: 'none',
        '&:active': {
          color: theme.palette.primary.main,
        },
        '&:hover': {
          textDecoration: 'underline',
        },
      },
    },
  })
);

interface Props {
  contents: string;
}
const MarkdownContents = ({ contents }: Props) => {
  const styles = useStyles();
  return (
    <div className={styles.root}>
      <MDXRenderer>{contents}</MDXRenderer>
    </div>
  );
};

export default memo(MarkdownContents);
