import React from 'react';
import { graphql } from 'gatsby';
import Page from 'components/Layout/Page';
import { Container, Grid, Button, makeStyles, Theme, createStyles } from '@material-ui/core';
import { Magazine as MagazineModel } from 'src/models';
import Markdown from 'components/Utils/Markdown';
import { useSpring, animated } from 'react-spring';
import SEO from 'components/Utils/SEO';

export const pageQuery = graphql`
  query($path: String!) {
    mdx(fields: { path: { eq: $path } }) {
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        title
        draft
        thumbnail {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      excerpt
    }
  }
`;

const useStyles = makeStyles<Theme>(theme =>
  createStyles({
    root: {
      maxWidth: 800,
      margin: '3rem auto',
    },
    titleWrapper: {
      borderBottom: `1px solid ${theme.palette.action.hover}`,
      marginBottom: '2rem',
      paddingBottom: '2rem',
    },
    title: {
      fontWeight: 700,
      color: theme.palette.text.primary,
      marginBottom: '1rem',
    },
    date: {
      fontSize: '1rem',
      margin: 0,
      fontWeight: 300,
    },
    footer: {
      marginTop: '5rem',
    },
    leftIcon: {
      marginRight: '1rem',
    },
    rightIcon: {
      marginLeft: '1rem',
    },
  })
);

interface Props {
  data: {
    mdx: MagazineModel;
  };
  pageContext: {
    next: Pick<MagazineModel, 'fields' | 'frontmatter'>;
    prev: Pick<MagazineModel, 'fields' | 'frontmatter'>;
  };
}

const Magazine = ({ data, pageContext }: Props) => {
  const styles = useStyles();
  const titleAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(-30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  });
  const descriptionAnimation = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateX(0)' },
    config: {
      duration: 500,
    },
    delay: 400,
  });

  const { mdx } = data;
  if (!mdx) {
    return <div>Render Error</div>;
  }

  const { frontmatter, body, excerpt } = mdx;
  const { prev, next } = pageContext;

  return (
    <Page pageName={frontmatter.title}>
      <SEO title={frontmatter.title} description={excerpt} image={frontmatter.thumbnail?.childImageSharp?.fluid.src} />
      <Container data-type="markdown" className={styles.root}>
        <Grid container>
          <Grid xs={12} sm={12} md={12} lg={12} item>
            <animated.div style={titleAnimation} className={styles.titleWrapper}>
              <h1 className={styles.title}>{`${frontmatter.title}${frontmatter.draft ? ' [ÏßëÌïÑÏ§ë]' : ''}`}</h1>
              <p className={styles.date}>{frontmatter.date}</p>
            </animated.div>
            <animated.div style={descriptionAnimation}>
              <Markdown contents={body} />
            </animated.div>
          </Grid>
        </Grid>
        <Grid container className={styles.footer} spacing={3} justify="space-between">
          <Grid item>
            {next && (
              <Button fullWidth href={next.fields.path} variant="outlined">
                <i className={`fas fa-angle-left ${styles.leftIcon}`}></i>
                {next.frontmatter?.title ?? 'Îã§Ïùå Îß§Í±∞ÏßÑ'}
              </Button>
            )}
          </Grid>
          <Grid item>
            {prev && (
              <Button fullWidth href={prev.fields?.path} variant="outlined">
                {prev.frontmatter?.title ?? 'Ïù¥Ï†Ñ Îß§Í±∞ÏßÑ'}
                <i className={`fas fa-angle-right ${styles.rightIcon}`}></i>
              </Button>
            )}
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default Magazine;
